  --form-btnTextHover: var(--black);
  --form-hightLightText: rgb(80, 79, 79);
  --form-hightLightMenu: var(--whitesmoke);
}

.formModal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
