}
.form__input {
  padding: 20px;
  text-align: left;
  outline: none;
  background-color: transparent;
  color: var(--white);
}
.form__btn {
  background-color: var(--form-btn);
  color: var(--form-btnText);
  cursor: pointer;
}
.form__btn:hover {
  background-color: var(--form-btnBckHover);
  color: var(--form-btnTextHover);
}

.formTab {
  width: 100%;
  min-height: 60px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 0.875rem;
  border: 1px solid var(--whitesmoke);
}
@media (min-width: 48rem) {
  .formTab {
    font-size: 1rem;
  }
}

.closingCross {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 1.125rem;
  color: var(--white);
  cursor: pointer;
  background-color: transparent;
  border: none;
}
.closingCross:hover {
  color: var(--lightGray);
